@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: "Press Start 2P";
}

#grid {
  overflow: scroll;
}

.App {
  text-align: center;
  background-attachment: fixed;
  background-position: center;
  background-repeat: repeat-x;
  background-repeat: repeat-y;
  min-height: 100vh;
}

.btn-primary {
  float: center;
  margin-top: 1em;
  margin-bottom: 1em;
  background-color: #3c5aa6;
}

.card {
  padding: 1em;
}

.col-sm {
  justify-content: center;
  overflow: auto;
  padding: 2%;
}

.container {
  border-radius: 25px;
  display: flex;
  background-color: whitesmoke;
  justify-content: center;
  margin-top: 1em;
  padding: 2em;
}

.header {
  background-color: #ffff33;
  overflow: hidden;
}

.logo {
  float: center;
  max-width: 80%;
  margin-left: 40px;
  margin-right: 40px;
  margin-bottom: 1em;
}

.name {
  border-radius: 25px;
  border-style: solid;
  border-width: 1em;
  background-color: white;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
  padding: 0.3em;
}

.roundedBox {
  border-radius: 25px;
  border-style: solid;
  background-color: white;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: 0.3em;
}

.progress {
  width: 15em;
}

.row {
  width: 100%;
}

.searchbar {
  float: right;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-right: 3em;
  margin-top: 3em;
  margin-bottom: 1em;
}

.tableCell,
.tableCell2 {
  display: table-cell;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 3px 10px;
}

.tableCell2 {
  font-size: smaller;
}

.tableRow {
  display: table-row;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

